<template>
  <!-- 打开一级，请求一级的目录列表（只显示文件夹） -->
<div id="FileLeft" :style="{width: widthNum + 'rem'}">
  <div id="content">
    <div class="folderName" v-on:click="chooseFolder()">
      <img :class="{rot: open || changeFolder}" src="../assets/folder.png" v-on:click="openFolder()"/>
      <span>{{ folderName }}</span>
    </div>
    <ul>
      <li v-show="open || changeFolder" v-for="folder in folders">
        <FileLeft class="fileLeft" :fileRoute="folder.fileRoute" :folder-name="folder.fileName"></FileLeft>
      </li>
    </ul>
  </div>
  <div id="drag" v-on:dragstart="dragStart($event)"
   draggable="true">
  </div>
</div>
</template>

<script>
import axios from 'axios'
export default {
  name: "FileLeft",
  props: ['folderName','fileRoute'],
  data: function(){
    return {
      open: false,
      requestingData: false,
      folders: [],
      allChild: [],
      width: 8
    }
  },
  methods: {
    openFolder: function(){
      if(this.$store.state.currentFolder == this.fileRoute){
        this.open = true;
        this.$store.state.currentFolder = this.fileRoute;
      }
      this.open = !this.open;
      if(!this.requestingData && this.folders.length <= 0 && this.allChild.length <= 0){
        this.requestingData = true;
        this.folders = [];
        this.allChild = [];
        // 请求文件列表后添加
        var _this = this;
        axios.get(this.fileRoute)
        .then(function (response){
          var array = eval(response.data);
          for(var i in array){
            if(array[i].fileType == "folder"){
              _this.folders.push({fileName: array[i].fileName, fileType: array[i].fileType, fileRoute: array[i].fileRoute})
            }
            _this.allChild.push({fileName: array[i].fileName, fileType: array[i].fileType, fileRoute: array[i].fileRoute})
          }
          _this.$store.state.ShowingFile = _this.allChild;
          _this.requestingData = false;
        }).catch(function (error){
          console.log(error);
          _this.requestingData = false;
        })
      }else {
        this.$store.state.ShowingFile = this.allChild;
      }
    },
    chooseFolder: function(){
      this.$store.state.ShowingFile = this.allChild;
      this.$store.state.currentFolder = this.fileRoute;
    },
    dragStart: function(event){
      event = event || window.event;
      var left = document.getElementById("FileLeft");
      var fileMain = left.offsetParent;
      document.onmousemove = (event) => {
        event = event || window.event;
        this.width = (event.clientX - fileMain.offsetLeft) / 16;
        if(this.width >= fileMain.clientWidth / 16 - 2)this.width = fileMain.clientWidth / 16 - 2;
        else if(this.width <= 6)this.width = 6;
      }
      document.onmouseup = (event) => {
        document.onmousemove = null;
        document.onmouseup = null;
      }
    }
  },
  computed: {
    changeFolder: function(){
      return this.open = (this.open || this.$store.state.currentFolder == this.fileRoute);
    },
    currentFolder: function(){
      return this.$store.state.currentFolder;
    },
    widthNum: function(){
      return this.width;
    }
  },
  watch: {
    currentFolder: function(){
      if(this.currentFolder == this.fileRoute){
        // this.folders = [];
        // for(var i in this.$store.state.ShowingFile){
        //   this.folders.push(this.$store.state.ShowingFile[i]);
        // }
        this.openFolder();
        this.open = true;
      }
    }
  }
}
</script>

<style scoped>
#FileLeft ul{
  margin: 0;
  list-style-type: none;
  padding-left: 1rem;
}
#FileLeft .folderName{
  cursor: pointer;
  display: flex;
}
#FileLeft .fileLeft {
  background-color: none;
  border-radius: 0.4rem;
}
.folderName:hover {
  background-color: rgba(10,10,10,0.08);
}
.folderName img{
  width: 1rem;
  height: 1rem;
  margin-top: 0.3rem;
}
.folderName span{
  white-space: nowrap;
  overflow: hidden;
  margin-left: 1rem;
  text-overflow: ellipsis;
}
.folderName .rot{
  transform: rotate(90deg);
}
#content {
  height: 98%;
  margin-right: 0.2rem;
  cursor: default;
  float: left;
}
#drag {
   background-color: white;
   height: 100%;
   width: 0.2rem;
   float: right;
   cursor: url('../assets/drag.png') 15 0, auto;
}
</style>



