<template>
<div id="FileRight">
  <ul class="files">
    <li v-for="file in files" v-on:dblclick="expand(file)">
      <img v-if="file.fileType == 'folder'" src="../assets/folderIcon.png" />
      <img v-else src="../assets/document.png" />
      <span>{{ file.fileName }}</span>
    </li>
  </ul>
</div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'FileRight',
  data: function(){
    return {
    }
  },
  computed: {
    files: function(){
      return this.$store.state.ShowingFile;
    }
  },
  methods: {
    // 打开文件夹 或者 下载文件
    expand: function(file){
      if(file.fileType == 'folder'){
        this.$store.state.currentFolder = file.fileRoute;
      }else {
        const elink = document.createElement('a');
        elink.href = file.fileRoute;
        document.body.appendChild(elink);
        elink.click();
        document.body.removeChild(elink);
      }
    }
  }
}
</script>

<style scoped>
#FileRight li{
  display: inline-flex;
  flex-direction: column;
  cursor: pointer;
  width: 5rem;
}
#FileRight li:hover{
  background-color: rgba(10,10,10,0.2);
  color: rgba(255,255,255,0.8);
  border-radius: 0.3rem;
}
#FileRight .files {
  list-style-type: none;
  display: inline-flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  float: left;
}
#FileRight img{
  width: 4.8rem;
}
#FileRight span{
  white-space: wrap;
  word-wrap: break-word;
}
</style>



