import Vue from 'vue'
import Router from 'vue-router'
import Main from '@/components/Main'
import File from '@/components/File'
Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      component: Main
    },
    {
      path: '/:files',
      component: Main,
      children: [
        {path: '/File', component: File}
      ]
    }
  ]
})



