<template>
<div id="FileTop">
  <div class="control">
    <img src="../assets/close.png">
    <img src="../assets/controlsize.png">
    <img src="../assets/minimize.png">
  </div>
  <div class="multifunction">
    <div class="searchtools">
    </div>
    <div class="chooseFile">
      <input type="file" id="file" v-on:change="upload"/>
      上传
    </div>
  </div>
</div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'FileTop',
  methods: {
    upload: function(){
      var file = document.getElementById("file").files[0],
      mname = file.name,
      size = file.size,
      shardSize = 2 * 1024 * 1024,
      shardCount = Math.ceil(size / shardSize);
      var faileds = new Array;
      for(let i = 0 ; i < shardCount ; i++){
        let start = i * shardSize,
        end = Math.min(size, start + shardSize);
        var form = new FormData();
        form.append(mname +":" + i + "/" + shardCount,file.slice(start,end));
        axios.post(this.$store.state.uploadAddress,form)
        .then(reponse => {

        })
        .catch(response => {
          faileds.push(form);
          console.log(response);
        })
      }
      while(faileds.length > 0){
        var x = faileds.pop();
        axios.post(this.$store.state.uploadAddress,x)
        .catch(response => {
          faileds.push(x);
        })
      }
      // let suc = new FormData();
      // suc.append(mname,"success");
      // axios.post(this.$store.state.uploadAddress,suc)
      // .then(response => {
      // })
      // .catch(response => {
      // });
    }
  }
}
</script>

<style scoped>
#FileTop {
  height: 3.8rem;
  background-color: rgba(255,255,255,0.6);
  position: relative;
}
.control {
  width: 100%;
  height: 1.4rem;
  margin: 0;
  padding: 0;
}
.control img{
  float:right;
}
.multifunction {
  width: 100%;
  height: 2.4rem;
  clear: both;
}
.searchtools {
  background-color: rgba(255,255,255,0.7);
  height: 2rem;
  margin: 0.1rem;
  width: 40%;
  display: inline-block;
  float: left;
  border-radius: 0.6rem;
}
.chooseFile {
  position: relative;
  width: 5rem;
  float: right;
  line-height: 2.4rem;
  background-color: rgba(0,134,139,0.8);
  color: white;
  border-radius: 0.2rem;
}
.chooseFile input{
  opacity: 0;
  position: absolute;
  width: 5rem;
}
</style>



