<template>
<div id="File">
  <FileTop ></FileTop>
  <FileLeft class="fileLeft" :fileRoute="/root/" folder-name="根目录"></FileLeft>
  <FileRight class="fileRight"></FileRight>
</div>
</template>

<script>
import FileTop from './FileTop'
import FileLeft from './FileLeft'
import FileRight from './FileRight'
export default {
  name: 'File',
  components: {FileLeft,FileRight,FileTop}
}
</script>

<style scoped>
.fileLeft {
  height: 100%;
  background-color: rgba(255,255,255,0.5);
  overflow: auto;
  float: left;
}
.fileRight{
  width: 80%;
  height: 100%;
  overflow: auto;
  float: left;
}
</style>



