<style scoped>
#main ul{
  display: flex;
  /* 竖直排列 */
  /* flex-direction: column; */
  /* 自动换行 */
  flex-wrap: wrap;
  height: 100%;
  margin:0;
  list-style-type: none;
}
#main li{
  display:inline;
  margin: 1rem;
  width: 7rem;
  cursor: pointer;
}
#main img{
  width: 6rem;
}
li div{
  margin: 0 auto;
  width: 6rem;
  color: white;
  background-color: rgba(0,0,0,0.2);
  border-radius: 2rem;
}
#main li:hover{
  background-color: rgba(0,0,0,0.2);
  border-radius: 0.4rem;
}
li:hover div{
  background-color: rgba(0,0,0,0);
}
#frame {
  width: 32rem;
  height: 32rem;
  background-color: #c1c1c1;
  overflow: hidden;
  position: absolute;
  z-index: 1;
  top: 0;left: 0;bottom: 0;right: 0;
  margin: auto;
  border-radius: 0.4rem;
}
</style>



